@import url('https://fonts.googleapis.com/css?family=Sora');

.App {
  background-color: rgba(33, 33, 33, 0.95);;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media only screen and (max-width: 1026px) {
  .navitems {
      display: none;
  }
}

.Loading-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Loading-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.MuiDrawer-paper {
  width: 50% !important;
  max-width: 300px;
}


.line{
  position: absolute;
  border-left: 1px solid #004e82;
  left: 10px;
  bottom: 0;
  top: 0;
}
.circle{
  width: 21px;
  height: 21px;
  line-height: 21px;
  border-radius: 50%;
  background: #004e82;
  position: relative;
  color: #fff;
  text-align: center;
}
.step {
  padding: 0 5px 50px 0;
  position: relative;
  min-height: 32px;
  display: flex;
}
.experience-stepper .step:last-of-type{
  padding: 0 5px 0 0;
}
@media (max-width: 700px) {
  .step {
    padding: 0;
  }
  .experience-stepper .step:last-of-type{
    padding: 0;
  }
}

.row {
  display: flex;
}

.column {
  flex: 50%;
}
 
  
.App-header {
  background-color: #23262cef;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-family:Arial, Helvetica, sans-serif;
  color: white;
}


.App-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.progress-div {
  background-color: rgb(233, 233, 233);
  border-radius: .5rem;
}

.progress {
  background-color: rgb(41, 98, 242);
  height: 10px;
  border-radius: 1rem;
  transition: 1s ease;
  transition-delay: 0.5s;
}

.progressBarPadding{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.binaryCalcHeader {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.submitButton{
  margin-top: 10%;
  width: 30%;
  padding-top: 2%;
  padding-bottom: 2%;
  background-color: #1b3e5e;
  color:black;
  max-width: 500px;
  border:none;
  border-radius:10px;
  box-shadow: 0px 0px 2px 2px rgb(0,0,0);
}

.submitButton:hover{
  background-color:#073867;
}

.inputField{
  width: 50%;
  margin-top: 10%;
}

.inputField:hover{
  background-color:#c5c5c5;
}

.inputField:focus{
  background-color:#c5c5c5;
}

.outputField{
  padding: 2%;
}

.infoSection{
  font-size: 0.5em;
}
